<template>

  <div class="content">
    <div class="head">
      <div class="head_left"> <img class="logo" src="../../assets/logo.png">
        <div class="icon">
          <img class="people" src="../../static/images/people.png">
        </div>
        <div class="name">
          <span>{{staff_name}}</span>
        </div>
        <div class="number">
          <span>{{staff_id}}</span>
        </div>
      </div>
      <div class="head_right">
        <div class="right_con">
          <div class="time">{{time}}</div>
          <div class="wifi"><img src="../../static/images/wifi.png" v-if="network"  /><img v-else
              src="../../static/images/no_wifi.png"></div>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="contain">

      <div class="main">
        <div class="name_contain">
          <div class="one_name" v-for="(item,index) in dataList" @click="toIndex(item.id,item.staff_name)" :key="index">
            <span>{{item.staff_name}}</span></div>
        </div>
        <div class="page_btn">
          <div class="top_jiantou" @click="pageReduce"><img src="../../static/images/shangjiantou.png"></div>
             <div class="current_page" ><span></span><span>{{page2}}/{{pageNumber}}</span><span></span></div>
          <div class="bottom_jiantou" @click="pageAdd"><img src="../../static/images/xiajiantou.png"></div>
        </div>
      </div>
      <div class="bottom_btn">
        <div class="btn_con">
          <div class="back_btn" @click="back"><img src="../../static/images/back.png"></div>
        </div>
      </div>
    </div>

  </div>



</template>
<script>
  import axios from 'axios'
  export default {
    name: "people",

    data: () => ({
      time: "", //
      staff_name: "", //
      staff_id: "", //id
      companyID: "", //ID
      id: "", //id
      network: true,
      page: 1,
          page2:1,//
      flag: true, //
      pageNum: 28,//pagesize
      pageNumber:null,//
      lastPath:"",
      dataList: [

      ]
    }),

    methods: {

      getTime() { //
        //;
        var dateTime = new Date();
        //;
        var year = dateTime.getFullYear();
        //;
        var month = dateTime.getMonth() + 1;
        //;
        var day = dateTime.getDate();
        //;
        var hours = dateTime.getHours();
        //;
        var minutes = dateTime.getMinutes();
        //;
        var second = dateTime.getSeconds();
        day < 10 ? day = '0' + day : day;
        month < 10 ? month = '0' + month : month;
        hours < 10 ? hours = '0' + hours : hours;
        minutes < 10 ? minutes = '0' + minutes : minutes;
        second < 10 ? second = '0' + second : second;

        let dateStr = year + '-' + month + '-' + day + ' ' + ' ' + hours + ':' + minutes + ':' + second;
        this.time = dateStr
      },
           back() { //
        // this.$router.push("/index/:"+this.$store.state.companyID+"/:"+this.$store.state.library_num)
        console.log(this.lastPath)
        if(this.lastPath=="index"){
          this.$router.push("/index/:"+this.$store.state.companyID+"/:"+this.$store.state.library_num)
        }else if(this.lastPath=="stockIn"){
            this.$router.push("/stockIn/:"+this.$store.state.companyID+"/:"+this.$store.state.library_num)
        }else if(this.lastPath=="stockOut"){
            this.$router.push("/stockOut/:"+this.$store.state.companyID+"/:"+this.$store.state.library_num)
        }else if(this.lastPath=="peopleIn"){
            this.$router.push("/peopleIn")
        }
      },
      toIndex(id, name) { //
      console.log(this.lastPath)
        this.$store.commit('setPeopleData', {
          staff_name: name,
          staff_id: id
        }) //
           this.$store.commit('setPasswordFlag', false)//
       if(this.lastPath=="index"){
          this.$router.push("/index/:"+this.$store.state.companyID+"/:"+this.$store.state.library_num)
        }else if(this.lastPath=="stockIn"){
            this.$router.push("/stockIn")
        }else if(this.lastPath=="stockOut"){
            this.$router.push("/stockOut")
        }else if(this.lastPath=="peopleIn"){
            this.$router.push("/peopleIn")
            
        }
      },
      getRootStaffOrganization() { //
        let url = "http://localhost:8206/api/staff/getRootStaffOrganization"

        let that = this
    
        let companyID = that.companyID
        axios({
          method: "post",
          url: url,
          timeout:1000,
          data: {    },
          headers: {
            companyID: companyID
          }
        }).then((res) => {
          //console.log(res.data.data.id);
          that.id = res.data.data.id;
          that.getStaffListByOrganization(that.id) //()
        }, function (err) {
       
     if (err == "Error: timeout of 1000ms exceeded"||"Error: Network Error") { //1000
            that.network = false
                that.$message({
              type: 'warning',
              message: ''
            });
          } else {

            that.network = true
          }
        })

      },
      getStaffListByOrganization(id) { //()
        let url = "http://localhost:8206/api/staff/getStaffListByOrganization"
        let that = this
        //console.log(that.companyID)
        let companyID = that.companyID
        axios({
          method: "post",
          url: url,
          data: {
            staff_organization_id: id,
            page: that.page,
            pageNum: that.pageNum
          },
          headers: {
            companyID: companyID
          }
        }).then((res) => {
          console.log(res);
              that.page2=that.page
                  that.pageNumber=Math.ceil(res.data.totalDataNum/that.pageNum)
          if (res.data.data.staffModel.length == 0) {
            this.$notify.info({
              title: '',
              duration: 2000,
              message: ''
            });
            that.page = that.page - 1 //
             that.page2=that.page //pagepage2
            that.flag = false //+
            return
          } else {
            that.flag = true
          }
          if (that.dataList.length > 0) {
            that.dataList = []
          }
          for (let i = 0; i < res.data.data.staffModel.length; i++) {
            that.dataList.push(res.data.data.staffModel[i])
          }
        })

      },
      pageReduce() { //

        if (this.page > 1) {
          this.page = this.page - 1
        }
        this.getStaffListByOrganization(this.id)
      },
      pageAdd() { //
        if (this.flag == true) { //
          this.page = this.page + 1
          this.getStaffListByOrganization(this.id)
        }

      },
    },
    mounted() {

     if (this.$store.state.companyID != "") { //storecompanyid
        this.companyID = this.$store.state.companyID
        this.library_num = this.$store.state.library_num
      } else {//
        this.companyID = this.$route.params.companyId;
        this.library_num = this.$route.params.library_num
      }
           if (this.$store.state.peopleData.staff_name) {
        this.staff_name = this.$store.state.peopleData.staff_name
        this.staff_id = this.$store.state.peopleData.staff_id
      }
      this.lastPath=this.$route.params.lastPath
      let timer = setInterval(this.getTime, 1000);
      this.getRootStaffOrganization()


    },


  }

</script>
<style scoped>
  .crmx_con /deep/ .el-table th,
  .el-table tr {
    background-color: rgb(179, 206, 248);
    color: black;
  }

  .crmx_con /deep/ .el-table--enable-row-transition .el-table__body td {
    background-color: rgb(179, 206, 248);
    color: black;

  }

  .kc_table /deep/ .el-table th,
  .el-table tr {
    background-color: rgb(179, 206, 248);
    color: black;
  }

  .kc_table /deep/ .el-table--enable-row-transition .el-table__body td {
    background-color: rgb(179, 206, 248);
    color: black;

  }

  .content {
    width: 100%;
    height: 100vh;

    background: rgb(33, 30, 124);
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  .contain {
    width: 100%;
  height: 83.5vh;
    display: flex;
    align-items: center;
    flex-direction: column;

  }



  /* */
  .head {
    width: 100%;
      height: 13vh;
    display: flex;

      align-items: center;

  }

 .line {
    background: white;
    width: 99%;
    height: 0.5vh;
  }

  .head_left {
    width: 50%;
    height: 100%;
    display: flex;
   align-items: flex-end;
  }

  .head_right {
    width: 50%;
    height: 100%;
    display: flex;
       align-items: center;
    justify-content: flex-end;
  }

  .logo {

    width: 300px;
    margin-bottom: 17px;
    margin-left: 2%;
  }

  .icon {
    height: 100%;
    width: 20%;
   margin-bottom: 17px;
    display: flex;
    justify-content: center;
   align-items: flex-end;
    margin-left: 25px;
  }

  .people {
    height: 50%;




  }

  .name {
    margin-bottom: 20px;
    background: rgb(22, 101, 227);
 min-width: 20%;
    min-height: 38%;
    border: 1.5px solid white;
    border-radius: 8px;
    display: flex;
    margin-left: 10px;
    justify-content: center;
    align-items: center;

  }

  .name span {
    font-size: 1.5em;
    font-weight: 600;
    color: white;
    word-break:normal; width:auto; display:block; white-space:pre-wrap;word-wrap : break-word ;overflow: hidden ;
  }

  .number {
 margin-bottom: 20px;
    height: 38%;
    width: 12%;
    margin-left: 5%;
    border: 1.5px solid white;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .number span {
    font-size: 1.4em;
    font-weight: 600;
    color: rgb(9, 222, 223);
  }

  .right_con {

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
  }

  .time {
     font-size: 1.5em;
    color: white;
    font-weight: 600;
  }

  .wifi {
    width: 30%;
    height: 100%;
  }

  .wifi img {
    width: 50%;
    height: 50%;
  }

  /* */
  .line {
    background: white;
    width: 99%;
    height: 5px;
  }

  /* */
  .main {
    display: flex;
    width: 100%;
    height: 60%;

  }

  .name_contain {
    width: 80%;
    margin: 3%;
    height: 100%;
    overflow: auto;

  }

  .page_btn {
    width: 10%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;


  }

  .top_jiantou {
      width: 72%;
    height: 28%;
    background: rgb(204, 190, 217);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .top_jiantou img {
    width: 69%;
  }
.current_page{
      width: 90%;
    height: 20%;
   color: white;
   font-size: 2em;
    display: flex;
  flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
  .bottom_jiantou {
    width: 72%;
    height: 28%;
    background: rgb(204, 190, 217);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .bottom_jiantou img {
    width: 69%;
  }

  .one_name {
       height: 17%;
    width: 13%;
    background: rgb(211, 209, 219);
    border-radius: 12px;
    display: flex;
    justify-content:center;
    align-items: center;
    margin: 0.6%;
    float: left;
   
  }

  .one_name span {
    color: black;
    font-size: 1.6em;
    display:block;
height: 50%;
      overflow: hidden ;
            text-overflow: ellipsis ;
            float: left;
  }

  .bottom_btn {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }

  .btn_con {
    width: 50%;
    display: flex;
    justify-content: flex-end;
  }

  .back_btn {
          width: 35%;
    height: 74%;
    background: rgb(173, 128, 29);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5%;

  }

  .back_btn img {
    width: 50%;
  }

  /* */

</style>



