              <div class="xtsz_btn" @click="clear"><span></span></div>
              <div class="xtsz_btn" @click="toPeopleIn"><span></span></div>
              <div class="xtsz_btn" @click="getCorrectTime"><span></span></div>
              <div class="xtsz_btn"><span></span></div>
            </div>
            <div class="system_num">V1.06</div>
          </div>

        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible3" width="50%" :show-close="showclose">
      <div style="display: flex;flex-direction: column;align-items: flex-start;height: 80%;    font-size: 2em;">
