      <!-- <el-drawer title="" :visible.sync="pm_visible2" direction="btt" :modal="pm_modal" :show-close="pm_showclose"
        size="61%">
        <vue-touch-keyboard style="font-size: 2em;" :options="pm_options" v-if="pm_visible2" :next="pm_next"
          :layout="pm_layout" :cancel="pm_hide" :accept="pm_accept" :input="pm_input" />
      </el-drawer> -->
      <span slot="footer" class="dialog-footer">
        <el-button style="    font-size: 3.5em;" @click="dialogVisible = false"> </el-button>
        <el-button style="    font-size: 3.5em;" type="primary" @click="query('pm')"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible4" width="50%" :show-close="showclose">
      <div style="display: flex;flex-direction: column;align-items: flex-start;height: 80%;    font-size: 2em;">
        <input type="text" style="width: 100%;height: 50%;font-size: 2em;    border: 1px solid black;"
          placeholder=""  v-model="SystemPassword" data-layout="normal" /></div>
      <!-- <el-drawer title="" :visible.sync="mm_visible2" direction="btt" :modal="mm_modal" :show-close="mm_showclose"
        size="61%">
        <vue-touch-keyboard style="font-size: 2em;" :options="mm_options" v-if="mm_visible2" :next="mm_next"
          :layout="mm_layout" :cancel="mm_hide" :accept="mm_accept" :input="mm_input" />
      </el-drawer> -->
      <span slot="footer" class="dialog-footer">
        <el-button style="    font-size: 3.5em;" @click="dialogVisible4 = false"> </el-button>
        <el-button style="    font-size: 3.5em;" type="primary" @click=" checkPassword()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible5" width="50%" :show-close="showclose" style="height: 85%;">
      <div style="display: flex;flex-direction: column;align-items: flex-start;height: 80%;    font-size: 2em;">
        <span>{{message}}</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button style="    font-size: 3.5em;" @click="dialogVisible5 = false"> </el-button>
        <el-button style="    font-size: 3.5em;" type="primary" @click="toInOut()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
  </div>



</template>

<script>
  import axios from 'axios'

  export default {
    name: "index",
    data: () => ({
      companyID: "", //ID
      library_num: "", //
      network: true, //
      timer: "", //
      time: "", //
      staff_name: "", //
      staff_id: "", //id
      page_size: 8, //pagesize
      page_num: 1, //pagenum
      page_num2: null, //
      flag: true, //
      id: "", //id
      password: "admin", //
      passwordFlag: null, //
      used_num: "", //
      style_num: "", //
      kc_pageBtn_show: false, //
      kc_pageNum: 1, //
      kc_pageSize: null, //
      pm_data: [], //
      style_name: "", //
      dialogVisible: false, //
      pm_search: "",
      dialogVisible2: false, //
      kw_search: "",
      dialogVisible3: false, //
      zh_search: "",
      dialogVisible4: false, //
      SystemPassword: "",
      dialogVisible5: false, //
      message: "",
      zt: "", //
      library_name: "",
      tabName: "", //tabname
      tabList: [ //
        {
          tabName: "",
          src: require("../../static/images/kcgk.png"),
          class: "one_chooseed"
        },
        {
          tabName: "",
          src: require("../../static/images/mx.png"),
          class: "one_choose"
        },
        {
          tabName: "",
          src: require("../../static/images/tongji.png"),
          class: "one_choose"
        },
        {
          tabName: "",
          src: require("../../static/images/xtsz.png"),
          class: "one_choose"
        }
      ],
      kcgk_show: true, // 
      crmx_show: false,
      kctj_show: false,
      xtsz_show: false,

      dataList: [], //
      tableData: [], //
      tableData2: [ //

      ],
      clearable: false,
      kw_visible2: false, //
      kw_visible: true, //
      kw_layout: "compact",
      kw_input: null,
      kw_modal: false,
      kw_showclose: false,
      kw_options: {
        useKbEvents: false,
        preventClickEvent: false
      },

      zh_visible2: false, //
      zh_visible: true, //
      zh_layout: "compact",
      zh_input: null,
      zh_modal: false,
      zh_showclose: false,
      zh_options: {
        useKbEvents: false,
        preventClickEvent: false
      },
      showclose: false,
      pm_visible2: false, //
      pm_visible: true, //
      pm_layout: "compact",
      pm_input: null,
      pm_modal: false,
      pm_showclose: false,
      pm_options: {
        useKbEvents: false,
        preventClickEvent: false
      },
      mm_visible2: false, //
      mm_visible: true, //
      mm_layout: "compact",
      mm_input: null,
      mm_modal: false,
      mm_showclose: false,
      mm_options: {
        useKbEvents: false,
        timers:null,
        preventClickEvent: false
      },
    }),


    methods: {
      clearValue(key) {
        console.log("dsa")
        if (key == "zh") {
          this.zh_input = null
          this.zh_search = ""
          console.log(this.zh_search)
        }
      },
      mm_accept(text) {

        console.log(this.SystemPassword)
        this.checkPassword()
        this.mm_hide();



      },

      mm_show(e) {
        this.mm_input = e.target;
        this.mm_layout = e.target.dataset.layout;

        if (!this.mm_visible2) {

          this.mm_visible2 = true
        }
        console.log(this.mh_visible2)


      },
      mm_next() { //
        this.mm_input.value = ""
        this.SystemPassword = ""

      },
      mm_hide() {

        this.mm_visible = false
        this.mm_visible2 = false
        this.dialogVisible4 = false

      },
      pm_accept(text) {
        // alert("Input text: " + text);
        console.log(this.pm_search)
        this.query('pm')
        this.pm_hide();



      },

      pm_show(e) {
        this.pm_input = e.target;
        this.pm_layout = e.target.dataset.layout;

        if (!this.pm_visible2) {

          this.pm_visible2 = true
        }
        console.log(this.zh_visible2)


      },
      pm_next() { //
        this.pm_input.value = ""
        this.pm_search = ""

      },
      pm_hide() {

        this.pm_visible = false
        this.pm_visible2 = false
        this.dialogVisible = false

      },
      zh_accept(text) {
        // alert("Input text: " + text);
        console.log(this.zh_search)
        this.query('zh')
        this.zh_hide();



      },

      zh_show(e) {
        this.zh_input = e.target;
        this.zh_layout = e.target.dataset.layout;

        if (!this.zh_visible2) {

          this.zh_visible2 = true
        }


      },
      zh_next() { //
        this.zh_input.value = ""
        this.zh_search = ""

      },
      zh_hide() {

        this.zh_visible = false
        this.zh_visible2 = false
        this.dialogVisible3 = false

      },
      kw_accept(text) {
        // alert("Input text: " + text);
        console.log(this.kw_search)
        this.query('kw')
        this.kw_hide();



      },
      kw_next() { //
        this.kw_input.value = ""
        this.kw_search = ""

      },
      kw_show(e) {
        this.kw_input = e.target;
        this.kw_layout = e.target.dataset.layout;

        if (!this.kw_visible2) {

          this.kw_visible2 = true
        }


      },

      kw_hide() {

        this.kw_visible = false
        this.kw_visible2 = false
        this.dialogVisible2 = false

      },
      inventoryInquiry(library_num, library_name, axis_name, style_name, type) { //
        let url = "http://localhost:8206/api/axis/inventoryInquiry"
        let that = this

        let selectInfo = {
          company_id: that.companyID
        }
        axios({
          method: "post",
          url: url,
          data: {
            selectInfo: selectInfo,
            axisLib: {
              library_num: that.library_num,
              library_name: library_name,
              axis_name: axis_name,
              style_name: style_name
            }
          },
          headers: {

          }
        }).then((res) => {
          that.tableData2 = [] //

          if (type == "pm") { //
            console.log(style_name)
            that.pm_data = []
            if (res.data.result.length == 0) {

              this.$message({
                type: 'info',
                message: ''
              });
            } else {


              for (let i = 0; i < res.data.result.length; i++) {
                if (res.data.result[i].is_take == 1) {
                  res.data.result[i].is_take = ""
                } else if (res.data.result[i].is_take == 0) {
                  res.data.result[i].is_take = ""
                }
                that.pm_data.push(res.data.result[i]) //pmdata
              }

            }


            that.kc_pageSize = Math.ceil(that.pm_data.length / 6) //
            for (let i = ((that.kc_pageNum * 6 - 1) - 5); i <= (that.kc_pageNum * 6 -
              1); i++) { //4tableData2

              if (that.pm_data[i]) {
                that.tableData2.push(that.pm_data[i])
              }

            }
          } else { //
            if (res.data.result.length == 0) {

              this.$message({
                type: 'info',
                message: ''
              });
            } else {


              for (let i = 0; i < res.data.result.length; i++) {
                if (res.data.result[i].is_take == 1) {
                  res.data.result[i].is_take = ""
                } else if (res.data.result[i].is_take == 0) {
                  res.data.result[i].is_take = ""
                }
                that.tableData2.push(res.data.result[i])
              }
              console.log(that.tableData2)
            }
          }




        })
        console.log(that.pm_input)
        if (that.pm_input) {
          that.pm_input.value = ""

        }
        if (that.kw_input) {
          that.kw_input.value = ""
        }
        if (that.zh_input) {
          that.zh_input.value = ""
        }
        that.kw_search = ""
        that.pm_search = ""
        that.zh_search = ""


      },
      pm_pageReduce() { //

        if (this.kc_pageNum > 1) {
          this.kc_pageNum = this.kc_pageNum - 1
          this.inventoryInquiry(this.library_num, "", "", this.style_name, "pm")
        }

      },
      pm_pageAdd() { //

        if (this.kc_pageNum < this.kc_pageSize) {
          this.kc_pageNum = this.kc_pageNum + 1
          this.inventoryInquiry(this.library_num, "", "", this.style_name, "pm")
        } else {
          this.$message({
            type: 'info',
            message: ''
          });
        }

      },
      inventoryStatistics() { //
        let url = "http://localhost:8206/api/axis/inventoryStatistics"
        let that = this


        axios({
          method: "post",
          url: url,
          data: {
            company_id: that.companyID,
            library_num: that.library_num
          },
          headers: {

          }
        }).then((res) => {

          that.used_num = res.data.result.used_num //
          that.style_num = res.data.result.style_num //



        })
      },
      getAllAxis() { //
        let url = "http://localhost:8206/api/axis/getAllAxis"
        let that = this
        let selectInfo = {
          company_id: that.companyID
        }
        //console.log(selectInfo)
        axios({
          method: "post",
          url: url,
          data: {
            selectInfo: selectInfo,
            library_num: that.library_num
          },
          timeout: 1000,
          headers: {}
        }).then((res) => {
          console.log(res)
          if (res.data.result.length == 0) {


            this.$message({
              type: 'info',
              message: ''
            });
          } else {

            that.dataList = []
            for (let i = 0; i < res.data.result.length; i++) {
              if (res.data.result[i].is_take == 1) {
                res.data.result[i].flag = true
              } else if (res.data.result[i].is_take == 0) {
                res.data.result[i].flag = false
              }
              that.dataList.push(res.data.result[i])
            }
          }


        }, function (err) {

          if (err == "Error: timeout of 1000ms exceeded" || "Error: Network Error") { //1000
            that.network = false
            this.$message({
              type: 'warning',
              message: ''
            });
          } else {

            that.network = true
          }
        })

      },
      getInAndOutRecord() { //
        let url = "http://localhost:8206/api/axis/getInAndOutRecord"
        let that = this

        axios({
          method: "post",
          url: url,
          data: {
            company_id: that.companyID,
            page_num: that.page_num,
            page_size: that.page_size
          },
          headers: {

          }
        }).then((res) => {
          console.log(res);
          that.page_num2 = Math.ceil(res.data.result.total_data_num / that.page_size)
          if (res.data.result.in_and_out_record_list.length == 0) {
            that.flag = false //+
            that.page_num = that.page_num - 1 //
            this.$message({
              type: 'info',
              message: ''
            });
          } else {
            that.flag = true //
            that.tableData = [] //
            for (let i = 0; i < res.data.result.in_and_out_record_list.length; i++) {
              if (res.data.result.in_and_out_record_list[i].is_take == 1) { //
                res.data.result.in_and_out_record_list[i].is_take = ""
              } else if (res.data.result.in_and_out_record_list[i].is_take == 0) {
                res.data.result.in_and_out_record_list[i].is_take = ""
              }
              that.tableData.push(res.data.result.in_and_out_record_list[i])
            }
          }

        })
      },
      clear() { //
        let url = "http://localhost:8206/api/axis/clearAxisLibraryRecord"
        let that = this

        axios({
          method: "post",
          url: url,
          data: {
            company_id: that.companyID
          },
          headers: {

          }
        }).then((res) => {

          if (res.data.message == "") {
            this.$message({
              type: 'success',
              message: ''
            });
          } else {
            this.$message({
              type: 'error',
              message: ''
            });
          }
        })
      },

      getTime() { //
        //;
        var dateTime = new Date();
        //;
        var year = dateTime.getFullYear();
        //;
        var month = dateTime.getMonth() + 1;
        //;
        var day = dateTime.getDate();
        //;
        var hours = dateTime.getHours();
        //;
        var minutes = dateTime.getMinutes();
        //;
        var second = dateTime.getSeconds();
        day < 10 ? day = '0' + day : day;
        month < 10 ? month = '0' + month : month;
        hours < 10 ? hours = '0' + hours : hours;
        minutes < 10 ? minutes = '0' + minutes : minutes;
        second < 10 ? second = '0' + second : second;

        let dateStr = year + '-' + month + '-' + day + ' ' + ' ' + hours + ':' + minutes + ':' + second;
        this.time = dateStr
      },
      tabChange(tabName) { //
        console.log(tabName)

        if (tabName != "") { //
          for (let i = 0; i < this.tabList.length; i++) { //

            this.tabList[i].class = "one_choose"

          }
          for (let i = 0; i < this.tabList.length; i++) { //
            if (this.tabList[i].tabName == tabName) {
