      checkPassword() {

        console.log(this.staff_id)
        if (this.staff_id) {
          let url = "http://localhost:8206/api/axis/verifyPassword"
          let that = this

          axios({
            method: "post",
            url: url,
            dataType: 'jsonp', // jsonp
            data: {
              selectInfo: {
                company_id: that.companyID
              },
              staff: {
                id: that.staff_id,
                password: that.SystemPassword
              }

            },
            headers: {

            }
          }).then((res) => {
            console.log(res)
            if (res.data.message == "") {
              that.passwordFlag = true //
              that.dialogVisible4 = false //
              that.$store.commit('setPasswordFlag', true)
              for (let i = 0; i < that.tabList.length; i++) { //

                that.tabList[i].class = "one_choose"

