                }
              }
              that.kcgk_show = false; //
              that.crmx_show = false;
              that.kctj_show = false;
              that.xtsz_show = true;
            } else {
              this.$message({
                type: 'error',
                message: ''
              });
            }
          }, function (err) {

            if (err == "Error: timeout of 1000ms exceeded" || "Error: Network Error") { //1000
              that.network = false
              this.$message({
                type: 'warning',
                message: ''
              });
            } else {

              that.network = true
            }
          })

        } else {

          this.$message({
            type: 'warning',
            message: ' '
          });


        }

        this.mm_input.value = ""
        this.SystemPassword = ""



      },
      StockinOut(library_name, flag) { //

        //console.log(flag)
        let zt;
        if (flag == false) {
          zt = ""
        } else {
          zt = ""
        }
        this.zt = zt
        this.message = library_name + '' + zt

        this.dialogVisible5 = true
        this.library_name = library_name


